.desktop-container {
    display: contents;

    .row-wrapper {
        display: flex;
        flex: 1 0 auto;
        margin: 0 !important;
    }

    .col-wrapper {
        flex: 1 0 auto;
        padding: 0 !important;
    }

    .search-col {
        background-color: $slate4;

        .collapsible {
            margin: 0 !important;

            &-body {
                background-color: $white !important;
                padding: 0.75rem !important;
            }
        }
    }

    .display-col {
        background-color: $white;
        display: flex;

        .loading-spinner, .pre-search-msg {
            margin: auto;
        }

        .pre-search-msg {
            color: $grey4;
            text-align: center;
        }

        .passage-text {
            margin: 1rem;

            h1 {
                font-size: 3rem;
            }

            h2 {
                font-size: 2.5rem;
            }

            h3 {
                font-size: 1.5rem;
            }

            .audio {
                float: right;
                font-size: 1rem;
            }
        }
    }
}